import React, { useCallback, useState, useEffect, FC, useMemo } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Box,
  Card,
  CardActions,
  makeStyles,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  CircularProgress,
  Avatar,
  Typography,
  Grid
} from '@material-ui/core';
import currency from 'currency.js';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import ClaimButton from '../ClaimButton';
import { useAirDropState } from 'src/hooks/useAirdropState';
import { commonFormatBalance, formatBalance } from 'src/utils/format';
import { BigNumber, FixedNumber, utils } from 'ethers';
import { AirdropReason } from 'src/utils/airdrop';
import { useTranslation } from 'react-i18next';
import { useRecoilValue } from 'recoil';
import { tokenPriceLoadingAtom, tokenPriceListAtom } from 'src/atoms';
import LoadingBox from 'src/components/LoadingBox';
import { getPriceBySymbol } from 'src/utils/price';
import { TBTC } from 'src/constants/token';
interface AirdropProps {
  className?: string;
}

const useStyles = makeStyles(theme => ({
  root: {},
  claim: {
    [theme.breakpoints.down('xs')]: {
      padding: '0 !important'
    }
  },
  avatar: {
    height: 32,
    width: 32,
    marginRight: theme.spacing(1)
  }
}));

const Airdrops: FC<AirdropProps> = ({ className, ...rest }) => {
  const classes = useStyles();
  const { t } = useTranslation();

  const isMountedRef = useIsMountedRef();
  const {
    airdropInfo,
    pendingAirdrops,
    asyncClaimClick,
    alreadyClaimed
  } = useAirDropState();
  const tokenPriceLoading = useRecoilValue(tokenPriceLoadingAtom);
  const tokenPrice = useRecoilValue(tokenPriceListAtom);

  const airdropTokenPrice = useMemo(() => {
    if (airdropInfo && airdropInfo.token) {
      return getPriceBySymbol(tokenPrice, airdropInfo.token.symbol);
    }
    return null;
  }, [airdropInfo, tokenPrice]);

  const total = useMemo(() => {
    if (!pendingAirdrops.result || !airdropInfo) {
      return BigNumber.from(0);
    }

    return Object.values(airdropInfo.details).reduce((current, item) => {
      return current.add(BigNumber.from(item));
    }, BigNumber.from(0));
  }, [pendingAirdrops.result, airdropInfo]);
  // let pendingAmount;
  // let total = BigNumber.from(0);
  // if (!pendingAirdrops.loading && pendingAirdrops.result && airdropInfo){
  //   pendingAmount = pendingAirdrops.result;
  //   Object.entries(airdropInfo.details).map(([type, amount]) =>{total = total.add(BigNumber.from(amount))});
  // }
  return (
    <Card className={clsx(classes.root, className)} {...rest}>
      <PerfectScrollbar>
        <Box minWidth={700}>
          {airdropInfo && pendingAirdrops.result && (
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>{t('airdrop.name')}</TableCell>
                  <TableCell>{t('airdrop.amount')}</TableCell>
                  <TableCell>{t('airdrop.approxUsd')}</TableCell>
                  <TableCell>{t('airdrop.reason')}</TableCell>
                  <TableCell>{t('airdrop.amount.total')}</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {Object.entries(airdropInfo.details).map(([type, amount]) => {
                  let rewardAmount = BigNumber.from(0);
                  let rewardValue = '0';
                  if (total && total.gt(0)) {
                    if (BigNumber.from(amount).gt(0)) {
                      rewardAmount = BigNumber.from(amount)
                        .mul(pendingAirdrops.result)
                        .div(total);
                    }
                  }

                  try {
                    rewardValue = FixedNumber.fromString(
                      utils.formatUnits(
                        rewardAmount,
                        airdropInfo.token.decimals
                      )
                    )
                      .mulUnsafe(
                        FixedNumber.fromString(
                          '' + getPriceBySymbol(tokenPrice, TBTC.symbol)
                        )
                      )
                      .toString();
                  } catch (e) {}

                  return (
                    <TableRow hover key={`${airdropInfo.index}-${type}`}>
                      <TableCell>
                        <Box display="flex" alignItems="center">
                          {airdropInfo.token.logoURI && (
                            <Avatar
                              className={classes.avatar}
                              src={airdropInfo.token.logoURI}
                            />
                          )}
                          <div>{airdropInfo.token.name}</div>
                        </Box>
                      </TableCell>
                      <TableCell>
                        {formatBalance(
                          rewardAmount,
                          airdropInfo.token.decimals
                        )}
                      </TableCell>
                      <TableCell>
                        <LoadingBox
                          loading={tokenPriceLoading}
                          p={0}
                          size={'25px'}
                        >
                          {currency(rewardValue).format()}
                        </LoadingBox>
                      </TableCell>
                      <TableCell>
                        {t('airdrop.program.' + AirdropReason[type])}
                      </TableCell>
                      <TableCell>
                        {formatBalance(
                          BigNumber.from(amount),
                          airdropInfo.token.decimals
                        )}
                      </TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          )}
        </Box>
      </PerfectScrollbar>
      <CardActions>
        <Grid container spacing={2} justify="space-between" alignItems="center">
          <Grid item xs={12} sm={6}>
            <Typography align="left" variant="h5" color="textPrimary">
              {t('airdrop.penddingClaimable')}{' '}
              {pendingAirdrops.result &&
              pendingAirdrops.result.gt(0) &&
              airdropInfo &&
              airdropTokenPrice
                ? formatBalance(
                    pendingAirdrops.result,
                    airdropInfo.token.decimals
                  ) +
                  ' ( ' +
                  currency(
                    FixedNumber.fromString(
                      utils.formatUnits(
                        pendingAirdrops.result,
                        airdropInfo.token.decimals
                      )
                    )
                      .mulUnsafe(FixedNumber.fromString('' + airdropTokenPrice))
                      .toString()
                  ).format() +
                  ' )'
                : '-'}
            </Typography>
            <Typography align="left" variant="h5" color="textPrimary">
              {t('airdrop.alreadyClaimed')}{' '}
              {alreadyClaimed.result &&
              alreadyClaimed.result.gt(0) &&
              airdropInfo &&
              airdropTokenPrice
                ? formatBalance(
                    alreadyClaimed.result,
                    airdropInfo.token.decimals
                  ) +
                  ' ( ' +
                  currency(
                    FixedNumber.fromString(
                      utils.formatUnits(
                        alreadyClaimed.result,
                        airdropInfo.token.decimals
                      )
                    )
                      .mulUnsafe(FixedNumber.fromString('' + airdropTokenPrice))
                      .toString()
                  ).format() +
                  ' )'
                : '-'}
            </Typography>
            <Typography align="left" variant="h5" color="textPrimary">
              {t('airdrop.ontheway')}{' '}
              {alreadyClaimed.result &&
              alreadyClaimed.result.gt(0) &&
              total &&
              total.gt(0) &&
              airdropInfo &&
              airdropTokenPrice
                ? formatBalance(
                    total.sub(alreadyClaimed.result).gt(0)
                      ? total.sub(alreadyClaimed.result)
                      : BigNumber.from(0),
                    airdropInfo.token.decimals
                  ) +
                  ' ( ' +
                  currency(
                    FixedNumber.fromString(
                      utils.formatUnits(
                        total.sub(alreadyClaimed.result).gt(0)
                          ? total.sub(alreadyClaimed.result)
                          : BigNumber.from(0),
                        airdropInfo.token.decimals
                      )
                    )
                      .mulUnsafe(FixedNumber.fromString('' + airdropTokenPrice))
                      .toString()
                  ).format() +
                  ' )'
                : '-'}
            </Typography>
          </Grid>
          <Grid item xs={12} sm={3} className={classes.claim}>
            <ClaimButton
              disabled={
                pendingAirdrops.loading ||
                !pendingAirdrops.result ||
                pendingAirdrops.result.isZero()
              }
              pending={asyncClaimClick.loading}
              onClick={asyncClaimClick.execute}
            />
          </Grid>
        </Grid>
      </CardActions>
    </Card>
  );
};

Airdrops.propTypes = {
  className: PropTypes.string
};

export default Airdrops;
