const BATCH_LOAD_RECORD_LIMIT = 5;
const RAMOUNT_DIVIDER = BigNumber.from(1e8).pow(BigNumber.from(4));
const TBTC_RATE = 1.0;

type Response<T> = {
  code: number;
  data: T;
  msg?: any;
};

export type TokenPriceList = {
  [key: string]: PriceItem;
};

export async function getAirdrops(account: string) {
  try {
    const response = await axios.get(
      `https://api.1-b.tc/api/airdrop/${account.toLowerCase()}`
    );

    const data = response.data?.data;
    if (!data || isEmpty(data)) {
      return null;
    }

    const airdrop: AirdropInfo = {
      token: TBTC,
      index: BigNumber.from(data.index),
      fullAmount: BigNumber.from(data.amount),
      proof: data.proof,
      flags: data.flags,
      details: data.details
    };

    return airdrop;
  } catch (e) {
    return null;
  }
}

