export async function getTokenPrice() {
  try {
    const response = await axios.get<Response<TokenPriceList>>(
      'https://api.1-b.tc/api/token/price'
    );
    const data = response.data.data;

    if (!data.BTCB) {
      data.BTCB = {
        max_supply: null,
        name: 'BTCB',
        price: data.BTC.price,
        symbol: 'BTCB',
        total_supply: null
      };
    }

    if (!data.TBTC) {
      data.TBTC = {
        max_supply: 2100,
        name: 'ùúèBitcoin',
        price: data.BTC.price * TBTC_RATE,
        symbol: 'TBTC',
        total_supply: null
      };
    }

    if (!data.DOGE) {
      data.DOGE = {
        max_supply: null,
        name: 'Doge',
        price: data.TDOGE.price,
        symbol: 'DOGE',
        total_supply: null
      };
    }

    return data;
  } catch (e) {
    return {};
  }
}

