export async function getNextRebaseDate(estPolicyContract: any) {
  if (estPolicyContract) {
    let nextTime = await getNextRebaseTimeInSeconds(estPolicyContract);
    let ms = nextTime.mul(1000).toNumber();
    if (ms < Date.now()) {
      return undefined;
    }
    console.log('nextTime', ms);
    return new Date(ms);
  }
  return undefined;
}

export async function getNextRebaseTimeInSeconds(estPolicyContract: any) {
  if (estPolicyContract) {
    const lastRebaseTime = await estPolicyContract.lastRebaseTimestampSec();
    const interval = await estPolicyContract.minRebaseTimeIntervalSec();
    return lastRebaseTime.add(interval);
  }
}

