export async function viewFarmBasicInfo(contract: any) {
  const data = await Promise.all(
    ['_farmStartedTime', '_miniStakePeriodInSeconds', '_farmDescription'].map(
      field => {
        try {
          return contract.callStatic[field]();
        } catch (e) {
          console.log(e);
          return '';
        }
      }
    )
  );
  return {
    started: data[0],
    stakePeriod: data[1],
    desc: data[2]
  };
}

export async function getDailyRecords(
  FarmContract: any,
  farmInfo: any,
  tokenAddress: string
) {
  let arr: number[] = [];
  let now = Date.now() / 1000;

  if (!farmInfo) {
    return undefined;
  }

  for (let index = 0; index < BATCH_LOAD_RECORD_LIMIT; index++) {
    let go = now - index * farmInfo.stakePeriod;
    if (go <= farmInfo.started) {
      break;
    }
    let k = getTimeKey(go, farmInfo.started, farmInfo.stakePeriod);
    arr.push(k);
  }
  let data = await Promise.all(
    arr.map(field => {
      try {
        return FarmContract.viewRoundSlot(field);
      } catch (e) {
        return '';
      }
    })
  );
  for (let index = 0; index < arr.length; index++) {
    data[index] = Object.assign({}, data[index], {
      timeKey: arr[index]
    });
    let rewardInfo = await FarmContract._roundSlotsReward(
      arr[index],
      tokenAddress
    );

    let rAmount = rewardInfo.rAmount;
    let rAccum = rewardInfo.rAccumulateAmount;
    if (rewardInfo.rAmount) {
      rAmount = rewardInfo.rAmount.div(RAMOUNT_DIVIDER);
    }
    if (rewardInfo.rAccumulateAmount) {
      rAccum = rewardInfo.rAccumulateAmount.div(RAMOUNT_DIVIDER);
    }
    data[index] = {
      rewardLastSubmiter: data[index]['rewardLastSubmiter'],
      rewardAmount: rAmount,
      rewardAccumulateAmount: rAccum,
      totalStaked: data[index]['totalStaked'],
      stakedLowestWaterMark: data[index]['stakedLowestWaterMark'],
      totalStakedInSlot: data[index]['totalStakedInSlot'],
      stakedAddresses: data[index]['stakedAddresses'],
      timeKey: arr[index]
    };
  }

  return data;
}

export async function getLatestRebaseRecord(contract: any) {
  try {
    if (!contract) {
      return {};
    }

    const recordLength = Number(
      BigNumber.from(await contract.getRebaseRecordsLen()).toNumber()
    );

    const record = await contract.rebaseRecords(recordLength - 1);

    return record;
  } catch (e) {
    console.log(e);
    return {};
  }
}

export async function getNewExpandRate(contract: any) {
  try {
    const base = await contract._getNewExpandBase();
    const team = await contract.teamRebaseSplit();

    const result = FixedNumber.from('1').subUnsafe(
      FixedNumber.from(team).divUnsafe(FixedNumber.from(base))
    );
    console.log('expand rate:', result.toString());
    return result;
  } catch (e) {
    console.log(e);
    return FixedNumber.from('0.6');
  }
}

export async function getLatestExpandAmount(
  chefContract: any,
  policyContract: any
) {
  if (!chefContract || !policyContract) {
    return '0';
  }

  const record = await getLatestRebaseRecord(policyContract);
  const rate = await getNewExpandRate(chefContract);

  if (FixedNumber.from(record.requestedSupplyAdjustment).isZero()) {
    return '0';
  }
  const rs = FixedNumber.from(record.requestedSupplyAdjustment)
    .mulUnsafe(rate)
    .toString();
  const dotIndex = rs.split('').findIndex(s => s === '.');

  const result = parseFixed(dotIndex > 0 ? rs.slice(0, dotIndex) : rs);
  return result.toString();
}
