import { useEffect, useMemo } from 'react';
import { useSetRecoilState, useRecoilValue } from 'recoil';
import {
  tDogeLatestExpandAmountAtom,
  tBTCLatestExpandAmountAtom
} from 'src/atoms';
import { getLatestExpandAmount } from 'src/service';
import {
  getTBTCESTPolicyContract,
  getTBTCChefContract
} from 'src/utils/contract';
import { contractConfig } from 'src/config';
import { useActiveWeb3React } from './useActiveWeb3React';
import { TDOGE, TBTC } from 'src/constants/token';
import { BigNumber } from 'ethers';

export function useFetchLatestExpandAmount() {
  const { library } = useActiveWeb3React();
  const setTDogeLatestExpandAmount = useSetRecoilState(
    tDogeLatestExpandAmountAtom
  );

  const setTBTCLatestExpandAmount = useSetRecoilState(
    tBTCLatestExpandAmountAtom
  );

  const TBTCSTPolicyContract = useMemo(() => {
    return getTBTCESTPolicyContract(contractConfig.TBTC_POLICY, library);
  }, [library]);

  const TBTCChefContract = useMemo(() => {
    return getTBTCChefContract(contractConfig.TBTC_CHEF, library);
  }, [library]);

  const TDOGEESTPolicyContract = useMemo(() => {
    return getTBTCESTPolicyContract(contractConfig.TDOGE_POLICY, library);
  }, [library]);

  const TDOGEChefContract = useMemo(() => {
    return getTBTCChefContract(contractConfig.TDOGE_CHEF, library);
  }, [library]);

  useEffect(() => {
    getLatestExpandAmount(TDOGEChefContract, TDOGEESTPolicyContract).then(
      data => {
        setTDogeLatestExpandAmount(data);
      }
    );
  }, [TDOGEESTPolicyContract, TDOGEChefContract]);


  useEffect(() => {
    getLatestExpandAmount(TBTCChefContract, TBTCSTPolicyContract).then(data => {
      setTBTCLatestExpandAmount(data);
    });
  }, [TBTCSTPolicyContract, TBTCChefContract]);
}

export function useGetLatestExpandAmount(symbol: string) {
  const tdogeAmount = useRecoilValue(tDogeLatestExpandAmountAtom);
  const tbtcAmount = useRecoilValue(tBTCLatestExpandAmountAtom);

  const result = useMemo(() => {
    if (symbol === TDOGE.symbol) {
      return tdogeAmount;
    }

    if (symbol === TBTC.symbol) {
      return tbtcAmount;
    }

    return '0';
  }, [symbol, tdogeAmount, tbtcAmount]);

  return result;
}
