import { TokenPriceList } from 'src/service';

export function getPriceBySymbol(list: TokenPriceList, symbol: string) {
  const item = Object.values(list).find(p => p.symbol.toLowerCase() === symbol);

  if (!item) {
    return undefined;
  }

  return item.price;
}

export function getTwapBySymbol(list: TokenPriceList, symbol: string) {
  const item = Object.values(list).find(p => p.symbol.toLowerCase() === symbol);

  if (!item) {
    return undefined;
  }

  return item.twap;
}
