import { BigNumberish, utils, FixedNumber } from 'ethers';
import { BTCB, BTCST, TBTC, TDOGE } from 'src/constants/token';
import { Token } from 'src/types/token';

const baseTime = 1616760000000;

export const formatBalance = (
  value: BigNumberish,
  decimals = 18,
  maxFraction = 0
) => {
  const formatted = utils.formatUnits(value, decimals);
  if (maxFraction > 0) {
    const split = formatted.split('.');
    if (split.length > 1) {
      return split[0] + '.' + split[1].substr(0, maxFraction);
    }
  }
  return formatted;
};

export function formatFixedBalance(
  value: BigNumberish,
  decimals = 18,
  maxFraction = 0
) {}

export function commonFormatBalance(value: BigNumberish, decimals = 18) {
  return formatBalance(value, decimals, 8);
}

export const parseBalance = (value: string = '0', decimals = 18) => {
  return utils.parseUnits(value, decimals);
};

export function getRoiAndApy(
  value: FixedNumber,
  price: FixedNumber,
) {
  const roi = Number(
    value
      .mulUnsafe(FixedNumber.from(365))
      .divUnsafe(price)
      .toString()
  );

  // const apy = (1+apr/365)^365-1;
  const daily = value.divUnsafe(price).addUnsafe(FixedNumber.from('1.0'));

  let apy = Math.pow(Number(daily.toString()), 365) - 1;

  return {
    roi,
    apy,
    daily
  };
}

export function formatApy(
  sourceToken: Token,
  value: FixedNumber,
  afterValue: FixedNumber,
  price: FixedNumber,
  targetToken?: Token
) {
  const { roi, apy } = getRoiAndApy(value, price);
  const { roi: afterRoi, apy: afterApy, daily: afterDaily } = getRoiAndApy(
    afterValue,
    price
  );

  let res = afterApy;
  let useRoi = false;
  if (targetToken && targetToken.symbol === TDOGE.symbol) {
    res = afterRoi;
    useRoi = true;
  }
  if (afterApy < 10000) {
    res = afterApy;
    useRoi = false;
  }
  console.log(
    `daily:${afterDaily},source:${sourceToken.symbol.toLowerCase()},targte:${
      targetToken ? targetToken.symbol : ''
    },value:${value},price:${price} res:${res} roi:${afterRoi} apy:${afterApy}`
  );

  const percent = formatPercentage(res, 2);
  const beforePercent = formatPercentage(useRoi ? roi : apy, 2) 
  const diff = FixedNumber.from(percent).subUnsafe(FixedNumber.from(beforePercent));

  return {
    data: `${percent}% ${useRoi ? '(ROI)': ''}`,
    diff,
  };
}

export const formatPercentage = (value: number, maxFraction = 2) => {
  const formatted = String(value * 100);
  if (maxFraction > 0) {
    const split = formatted.split('.');
    if (split.length > 1) {
      return split[0] + '.' + split[1].substr(0, maxFraction);
    }
  }
  return formatted;
};

export function getRemainTime() {
  const now = Date.now();
  const secondsPerDay = 24 * 60 * 60;
  const totalSeconds = Math.floor(((now - baseTime) / 1000) % secondsPerDay);
  const remainSeconds = secondsPerDay - totalSeconds;

  const hours = String(Math.floor((remainSeconds / (60 * 60)) % 24)).padStart(
    2,
    '0'
  );
  const minutes = String(Math.floor((remainSeconds / 60) % 60)).padStart(
    2,
    '0'
  );
  const seconds = String(Math.floor(remainSeconds % 60)).padStart(2, '0');
  const progress = Math.floor((totalSeconds * 100) / secondsPerDay);

  return {
    hours,
    minutes,
    seconds,
    progress
  };
}

export function formatMoney(num: Number) {
  return num.toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,');
}
