import React, { FC } from 'react';
import {
  Container,
  Grid,
  Hidden,
  makeStyles,
  Typography
} from '@material-ui/core';
import { useTheme } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useWeb3React } from '@web3-react/core';
import Page from 'src/components/Page';
import Header from 'src/components/Header';
import { Theme } from 'src/theme';
import Airdrops from './Airdrops';
import TDogeAirdrop from './TDogeAirdrops';
import MineCard from './MineCard';
import MarketMake from 'src/components/MarketMake';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    paddingBottom: 80,
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%'
    // paddingTop: theme.spacing(3),
    // paddingBottom: theme.spacing(3)
  },

  title: {
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(3),
    wordBreak: 'break-word'
  }
}));

const DashboardView: FC = () => {
  const classes = useStyles();
  const { account } = useWeb3React();
  const { t } = useTranslation();
  const theme = useTheme();

  const title = t('airdrop.title');

  return (
    <Page className={classes.root} title={t('global.title')}>
      <Container maxWidth={false}>
        <Header title={title} />
        <Hidden lgUp>
          <Typography variant="h3" color="textPrimary">
            {/* <SvgIcon fontSize="small">
                <SendIcon />
              </SvgIcon> */}
            {title}
          </Typography>
        </Hidden>
        <Typography
          className={classes.title}
          variant="body1"
          color="textPrimary"
        >
          {t('airdrop.avaliableFor').replace('#', account)}
        </Typography>
        <Grid container spacing={3}>
          <Grid item lg={12} xl={12} xs={12}>
            <Airdrops />
          </Grid>
        </Grid>
        {account && (
          <Grid container spacing={3}>
            <Grid item lg={12} xl={12} xs={12}>
              <TDogeAirdrop />
            </Grid>
          </Grid>
        )}

        <Typography className={classes.title} variant="h4" color="textPrimary">
          {t('airdrop.getMore')}
        </Typography>
        <Grid container spacing={3}>
          <Grid item lg={6} xl={6} xs={12}>
            <MineCard />
          </Grid>
          <Grid item lg={6} xl={6} xs={12}>
            <MarketMake />
          </Grid>
        </Grid>
      </Container>
    </Page>
  );
};

export default DashboardView;
