import { useMemo, useEffect, useState } from 'react';
// import { AirdropInfo } from 'src/utils/airdrop';
import { useActiveWeb3React } from './useActiveWeb3React';
import { contractConfig } from 'src/config';
import { getTBTCChefContract } from 'src/utils/contract';
import { useAsync, useAsyncCallback } from 'react-async-hook';
import { BigNumber } from 'ethers';
import { getTDOGEAirdrops } from 'src/service';
import useInterval from '@use-it/interval';

// TODO: mock here

export function useAirDropState() {
  const { account, library } = useActiveWeb3React();
  const [txHappend, setTxHappend] = useState(0);
  const [refetchKey, setRefetchKey] = useState(0);

  const airdropInfoData = useAsync(() => {
    if (!account) {
      return undefined;
    }
    return getTDOGEAirdrops(account);
  }, [account]);

  const airdropInfo = useMemo(() => {
    if (!airdropInfoData.result) {
      return undefined;
    }

    return airdropInfoData.result;
  }, [airdropInfoData.result]);

  const TBTCChefContract = useMemo(() => {
    return getTBTCChefContract(contractConfig.TDOGE_CHEF, library, account);
  }, [account, library]);

  const pendingAirdrops = useAsync<BigNumber>(
    () => {
      if (!airdropInfo) {
        return undefined;
      }
      return TBTCChefContract.pendingAirdrop(
        airdropInfo.index,
        account,
        airdropInfo.fullAmount,
        airdropInfo.proof
      );
    },
    [airdropInfo, TBTCChefContract, account, txHappend, refetchKey],
    {
      setLoading: state => ({ ...state, loading: true })
    }
  );

  const alreadyClaimed = useAsync<BigNumber>(() => {
    if (!account || !TBTCChefContract) {
      return;
    }

    return TBTCChefContract.merkleAlreadyClaimed(account);
  }, [TBTCChefContract, account]);

  const asyncClaimClick = useAsyncCallback(async () => {
    if (!airdropInfo) {
      return;
    }
    const gasLimit = await TBTCChefContract.estimateGas.claim(
      airdropInfo.index,
      account,
      airdropInfo.fullAmount,
      pendingAirdrops.result,
      airdropInfo.proof
    );

    const tx = await TBTCChefContract.claim(
      airdropInfo.index,
      account,
      airdropInfo.fullAmount,
      pendingAirdrops.result,
      airdropInfo.proof,
      { gasLimit: gasLimit.mul(150).div(100) }
    );

    if (tx) {
      await tx.wait();
      setTxHappend(t => t + 1);
    }
  });

  useInterval(() => {
    if (account) {
      setRefetchKey(c => c + 1);
    }
  }, 30000);

  return {
    airdropInfo,
    pendingAirdrops,
    asyncClaimClick,
    alreadyClaimed
  };
}
